import rTitle from '@/components/common/RHX/rTitle/rTitle';
export default {
	props: [
		'disabled',
		'itemsData',
		'indexNum',
		'dataList',
	],
	data() {
		return {
			dataLists: this.dataList,
		};
	},
	components: {
		rTitle,
	},
	computed: {},
	watch: {
		dataList: {
			handler(a, b) {
				console.log(a, b, '--------------------------------------dataList---------------------------------------');
				this.dataLists = a;
			},
			deep: true
		},
	},
	created() {
	},
	mounted() {},
	methods: {
		// 修改
		tapTitleTwo(e) {
			console.log(e);
			this.dataLists.content=e[1];
			this.$emit('tap-StyleSelectionList', this.dataLists);
		},
	}
};
